@import '../../../index.scss';
.reviewslist{


    .rewbox{
        width: 360px;
        height: 500px;   
        border: 1px solid #cfcfcf;
        margin-bottom: 110px;
    .usercomment{
        h4{
            padding: 10px 0;
        }
        .userphotos{
            width: 106px;
            height: 146px;
            margin: 10px 0;
        }
    }
    .userinfo{
        display: flex;
        padding-top: 40px;
        img{
            height: 64px;
            width: 64px;
        }
        .titlebox{
            margin: 0;
            padding: 0;
            h2{
                padding-left: 20px;
                @include headlinesecondfont(
                    15px, 
                    #5e81a7, 
                    'Open Sans, sans-serif',
                    bold,
                    normal
                    );
            }
            h3{
                padding-left: 20px;
                @include headlinesecondfont(
                    13px, 
                    #999999, 
                    'Myriad Pro',
                    normal,
                    normal
                    );
            }
        }
    }
    .iconsbar{
        margin: 15px 0;
        i{
            margin-right: 10px;
            color: #bacada;
            &:hover {color: rgba(0, 37, 78, 0.966)}
        }
    }
    .buttonblock{
        .buttoncsm{
            height: 30px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #5e81a7;
            color: white;
            cursor: pointer;
            text-decoration: none;
            outline: none;
            border: none;
            border-radius: 5px;
            &:hover {background-color: rgba(0, 37, 78, 0.966)}
            h5{
                margin: 0;
                margin-left: 10px;
            }
        }
    }
    }
    .rewborder{
        width: 270px;
        height: 430px;
        margin: 0 auto;
        align-items: center;
        justify-content: center;  
    }

}

