$activeColor: rgb(30, 223, 5);
$mainColor: #32505a;
$buttonColor: #fd4f10;



@mixin headlinefont(
    $size: 60px, 
    $color: $activeColor,
    $family: 'Lemon Tuesday',
    $weight: normal,
    $style: normal
    ) {
        color: $color;
        font-size: $size;
        font-family: $family;
        font-weight: $weight;
        font-style: $style;
  }

  @mixin headermainfont(
    $size: 14px, 
    $color: black,
    $family: 'Myriad Pro',
    $weight: normal,
    $style: normal
    ) {
        color: $color;
        font-size: $size;
        font-family: $family;
        font-weight: $weight;
        font-style: $style;
  }

  @mixin mainmenufont(
    $size: 14px, 
    $color: black,
    $family: 'Myriad Pro',
    $weight: bold,
    $style: normal
    ) {
        color: $color;
        font-size: $size;
        font-family: $family;
        font-weight: $weight;
        font-style: $style;
  }

  @mixin headlinesecondfont(
    $size: 27px, 
    $color: white,
    $family: 'Open Sans, sans-serif',
    $weight: lighter,
    $style: italic
    ) {
        color: $color;
        font-size: $size;
        font-family: $family;
        font-weight: $weight;
        font-style: $style;
  }