@mixin buttontext(
    $size: 14px, 
    $color: white,
    $family: 'Open Sans, Bold',
    $weight: 600,
    $style: normal
    ) {
        color: $color;
        font-size: $size;
        font-family: $family;
        font-weight: $weight;
        font-style: $style;
  }

.button{
    display: flex;
    align-items: center;
    justify-content: center;
.itemsliderbutton {
    height: 40px;
      width: 190px;
      cursor: pointer;
      text-align: center;
      text-decoration: none;
      outline: none;
      background-color: #fd4f10;
      border: none;
      border-radius: 5px;
      @include buttontext();
      text-align: center;
      i{
        font-size: 20px;
        padding-left: 40px;
      }
    }
    
    .itemsliderbutton:hover {background-color: #bf3e0f}
    
    .itemsliderbutton:active {
      background-color: #bf3e0f;
      box-shadow: 0 1px #666;
      transform: translateY(1px);
    }}

