footer{
    height: auto;
    color: #fff;
    background-color: #1a2d34;
    .footerColumns{
        margin: 0 auto;
        max-width: 1200px;
        display: flex;
        ul{
            flex: 50%;
            align-content: center;
            li{
               text-decoration: none;
               i {
                padding: 20px;
                font-size: 30px;
                width: 30px;
                text-align: center;
                text-decoration: none;
                margin: 5px 2px;
                border-radius: 50%;
                z-index: 1;
                &:hover {
                    opacity: 0.7;
                } }
              
            
               } 
            }
        }
        
    }
    
    
