@import '../../index.scss';

.container {
    margin: 0;
    padding: 0;
  }
  .item {
    padding: 5px;
    width: 200px;
    height: 150px;
    margin: 10px;
    color: white;
    font-weight: bold;
    font-size: 15px;
    text-align: center;
  }

  .itemtext{
        h1{
        @include  headlinesecondfont(
          16px, #949494, 'Open Sans', regular, normal);
    }
  }
  .flex {
    padding: 0;
    margin: 0;
    list-style: none;
    
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    
    -webkit-flex-flow: row wrap;
    justify-content: space-around;
  }